import React, { useEffect } from 'react'
import BuySectionOne from './BuySectionOne'
import BuySectionTwo from './BuySectionTwo'
// import BuySectionThree from './BuySectionThree'
import BuySectionFour from './BuySectionFour'
// import BuySectionFive from './BuySectionFive'
import BuySectionSix from './BuySectionSix'
import BuySectionSeven from './BuySectionSeven'
